.profileInfo{
    display: flex;
    flex-direction: row;
    padding: 50px;
}
.avatarContainer{
    position: relative;
    margin-right: 50px;
    
    width: 200px;
}
.changePhoto{
    position: absolute;
    bottom: 0px;
    color: rgb(237, 238, 240);
    
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 50px;
    transition: .5s all;
    opacity: 0;
}
.avatarContainer:hover  .changePhoto{
    opacity: 1;
}

.avatar{
    height: 200px;
    width: 200px;

}
.profileInfoContainer{
    border-bottom: 2px solid #EBEBEB;
}

.changePhotoInput{
    display: none;
}
.changePhotoLabel{
    display: block;
    text-align: center;
    font-family: Raleway;
    padding: 10px;
    cursor: pointer;
}
.changePhotoLabel:hover {
    color: #fff;
}
