.userMenu{
    position: absolute;
    top: 35%;
    right: 50px;
    background: url(../../img/follow-menu.png) no-repeat ;
    width: 6px;
    height: 24px;
    cursor: pointer;
}
.hideMenu{
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 5px;
    background-color: #fff;
    box-shadow: 0 3px 0px rgba(211,216,224,.15);
    border: 1px solid #EBEBEB;
    min-width: 140px;
    
    z-index: 100;
}
.userMenu:hover .hideMenu{
    visibility: visible;
}
.hideMenu button:hover {
    background-color: #e4eaf0;
}

.hideMenu button{
    
        /*Reset's every elements apperance*/
        background: none repeat scroll 0 0 transparent;
        border: medium none;
        border-spacing: 0;
        color: #26589F;
        font-family: 'PT Sans Narrow',sans-serif;
        font-size: 16px;
        font-weight: normal;
        line-height: 1.42rem;
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;
        text-decoration: none;
        text-indent: 0;
        display: block;
        width: 100%;
        cursor: pointer;
}
.popup{
    margin-left: auto;
    margin-right: auto;
    margin-top: 200px;
    background: white;  
    max-width: 600px;
    min-height: 337px;
    border-radius: 4px;
}
.headerPopup{
