.body{
  background-color: red;
}
.app-wrapper{
  display: grid;
  width: 100%;
  
  min-height: 100vh;
  grid-template-columns: 2fr 10fr;

  font-family: Raleway;
     
}



.app-wrapper-content{
  background-color: #F6F6F9;

  box-shadow: 0 3px 25px rgba(0,0,0,.15) ;
  z-index: 1;
  padding-bottom: 50px ;       
}

.sideBar{
  background-color: #F2F2F5;
  
}