.popup {  
    position: fixed;  
    width: 100%;  
    height: 100%;  
    top: 0;  
    left: 0;  
    right: 0;  
    bottom: 0;  
    margin: auto;  
    background-color: rgba(0,0,0, 0.5);  
    z-index: 9999;
  }  
  .popupInner {  
    position: absolute;  
    left: 25%;  
    right: 25%;  
    top: 20%;  
   
    margin-left: auto;
    margin-right: auto;  
    border-radius: 20px;  
    
  }
  .closePopup{
    position: absolute;
    right: -14px;
    top: -14px;
    background: url(../../../img/close.png) no-repeat;
    width: 32px;
    height: 32px;
    cursor: pointer;
  }